import * as PIXI from 'pixi.js';
import { Wheel } from './GameObjects/Wheel';
import Assets from './constants/Assets';
import * as QueryString from 'query-string';

export interface IConfig {
	segments: number;
	maxSegments: number;
	won: boolean;
	redirect?: string;
}

export default class Casino extends PIXI.Application
{
	static DARK:number = 0x0d0d0d;
	static LIGHT:number = 0x19191a;
	static HIGHLIGHT:number = 0x26e2c6;

	input:PIXI.interaction.InteractionManager;

	config:IConfig;

	wheel:Wheel;
	hasSpun:boolean;

	constructor(config:IConfig)
	{
		super({
			view: <HTMLCanvasElement>document.getElementById('canvas'),
			transparent: true,
			width: 720,
			height: 720,
			antialias: true,
		});

		// document.body.appendChild(this.view);
		this.hasSpun = false;
		this.config = config;
		this.input = new PIXI.interaction.InteractionManager(this.renderer);

		const background = PIXI.Sprite.fromImage(Assets.BACKGROUND);
		background.anchor.set(0.5);
		background.position.set(this.screen.width / 2, this.screen.height / 2);
		// this.stage.addChild(background);

		const all = new PIXI.Container();

		const casee = PIXI.Sprite.fromImage(Assets.WHEEL_CASE);
		casee.scale.set(0.5, 0.5);
		casee.anchor.set(0.5, 0.5);
		casee.position.set(this.screen.width / 2, (this.screen.height / 2) + 35);
		all.addChild(casee);

		all.addChild(this.wheel = new Wheel(config));
		this.wheel.position.set(this.screen.width / 2, this.screen.height / 2);
		this.wheel.scale.set(1, 1 * 0.85);


		const caseea = PIXI.Sprite.fromImage(Assets.WHEEL_CAP_OFF);
		caseea.scale.set(0.5, 0.5);
		caseea.anchor.set(0.5, 0.5);
		caseea.position.set(this.screen.width / 2, (this.screen.height / 2) );
		// all.addChild(caseea);

		all.scale.set(1);
		all.position.set(0, -30);
		this.stage.addChild(all);

		this.ticker.add((deltaTime) => this.update(deltaTime));
	}

	public spin() {
		if(this.hasSpun == false) {
			this.wheel.spin();
			this.hasSpun = true;
		}
	}

	update(deltaTime:number)
	{
		this.input.update(deltaTime);
	}
}

const params = QueryString.parse(location.search);
console.log(params);
const config = {
	won: params.won || false,
	maxSegments: params.maxSegments || 4,
	segments: params.segments || 2,
	redirect: params.redirect || false,
};

const casino = new Casino(config);

const button = document.createElement('div');
button.className = 'spinButton';
button.innerHTML = "SPIN";
button.onclick = () => {
	casino.spin();
	button.classList.add('spun');
}
document.body.appendChild(button);

// HMR Reload window
if (module.hot) {
	module.hot.dispose(() => {
		window.location.reload(true);
	});

	module.hot.accept(() => {});
}
