import { Linear, Sine, TimelineMax, TweenConfig, TweenMax } from 'gsap';
import * as PIXI from 'pixi.js';
import Assets from '../constants/Assets';
import { IConfig } from '..';
import MathHelper from '../utils/MathHelper';

export class Wheel extends PIXI.Container
{
	indicator:PIXI.Sprite;
	wheel:RoulettePlate;
	prize:PIXI.Sprite;
	cap_glow:PIXI.Sprite;

	locked:boolean = false;
	rotationOffset:number = 0;

	config:IConfig

	constructor(config:IConfig)
	{
		super();

		this.config = config;

		const scase = PIXI.Sprite.fromImage(Assets.CASE);
		scase.anchor.set(0.5, 0.5);

		this.prize = PIXI.Sprite.fromImage(Assets.PRIZE_SECTION_1);
		this.prize.anchor.set(0, 1);
		this.prize.rotation = Math.PI / 2;
		this.prize.scale.set(0.5, 0.5);
		this.prize.alpha = 0;
		this.prize.blendMode = PIXI.BLEND_MODES.ADD_NPM;

		this.wheel = new RoulettePlate(config);

		this.indicator = PIXI.Sprite.fromImage(Assets.INDICATOR);
		this.indicator.anchor.set(0.5, 1);
		this.indicator.scale.set(0.5, 0.5);

		const capoff = PIXI.Sprite.fromImage('wheel_cap_off.png');
		capoff.anchor.set(0.5, 0.5);
		capoff.scale.set(0.5, 0.5 * 1.25);

		this.cap_glow = PIXI.Sprite.fromImage('wheel_cap_win.png');
		this.cap_glow.anchor.set(0.5, 0.5);
		this.cap_glow.scale.set(0.5, 0.5 * 1.25);
		this.cap_glow.alpha = 0;

		this.addChild(this.wheel)
		this.addChild(this.indicator)
		this.addChild(capoff);
		this.addChild(this.cap_glow);
		this.wheel.addChild(this.prize);
	}

	public spin() {
		const timeline = new TimelineMax();

		// Start up
		const START = 'start';
		const SPIN = 'spin';
		const END = 'end';

		const toRadians = (angle: number) => angle * Math.PI / 180;

		const rotation = (value) => `+=${(Math.PI * 2) * value}`;

		timeline.addLabel(START, 0);
		timeline.to(this.wheel, 2, { rotation: rotation(-2), ease: Sine.easeIn }, START);
		timeline.to(this.indicator, 2, { rotation: rotation(2), ease: Sine.easeIn }, START);

		timeline.addLabel(SPIN, 2);
		timeline.to(this.wheel, 2, { rotation: rotation(-3), ease: Linear.easeNone, repeat: 0 }, SPIN);
		timeline.to(this.indicator, 2, { rotation: rotation(3), ease: Linear.easeNone, repeat: 0 }, SPIN);

		timeline.addLabel(END, 2 + 2);
		timeline.to(this.wheel, 2, { rotation: rotation(-2 - (!this.config.won ? 0 : .25) ), ease: Sine.easeOut }, END);
		timeline.to(this.indicator, 2, { rotation: rotation(2.12), ease: Sine.easeOut }, END);

		timeline.addCallback(() => {
			if(this.config.won) {
				this.tween(this.prize, 500, {
					alpha: 1,
					yoyo: true,
					repeat: 4,
					ease: Sine.easeInOut
				});
				this.tween(this.cap_glow, 500, {
					alpha: 1,
					ease: Sine.easeInOut
				});
			}

			setTimeout(() => {
				if(this.config.redirect) {
					window.location.href = window.atob(this.config.redirect);
				}
			}, 2000);

		}, END + 2)
	}

	public update(deltaTime:number) {
		if(this.locked) {
			this.indicator.rotation = this.rotationOffset + this.wheel.rotation;
		}
	}

	tween<T>(target:T | T[], duration:number, props:Partial<T | TweenConfig>){
		TweenMax.to(target, duration / 1000, props)
	}
}

class RoulettePlate extends PIXI.Container
{
	static LIGHT = 0xcfcfcf;
	static MEDIUM = 0xa9a9a9;
	static DARK = 0x7b7b7b;
	static DARKER = 0x6d6d6d;

	static BLUE_DARK = 0x89d1bc;
	static BLUE_LIGHT = 0x629d8c;

	constructor(config:IConfig)
	{
		super();

		const segments = config.maxSegments;
		const size = 275;
		const smallSize = 200;
		const line_width = 3;
		const segmentsComplete = config.segments;

		const gfx = new PIXI.Graphics();
		gfx.beginFill(RoulettePlate.LIGHT)
		gfx.lineStyle(line_width, RoulettePlate.DARK)
		gfx.drawCircle(0, 0, size);

		const angle = 360 / segments;

		for (let index = 0; index < segments; index++)
		{
			const prevRad = this.toRadians(angle * (index - 1));
			const rads = this.toRadians(angle * index);

			let points:PIXI.Point[] = [
				new PIXI.Point(0, 0),
			];

			const splits = 10;
			const splitAngle = angle / splits;

			for (let a = 0; a < splits ; a++)
			{
				const AprevRad = this.toRadians(splitAngle * (a)) + this.toRadians(angle * (index));
				const arads = this.toRadians(splitAngle * (a + 1)) + this.toRadians(angle * index + 1);

				points.push(MathHelper.vectAngle(AprevRad, size) as PIXI.Point);
				points.push(MathHelper.vectAngle(arads, size) as PIXI.Point);
			}

			points.push(new PIXI.Point(0, 0));
			gfx.beginFill(index % 2 != 0 ? RoulettePlate.MEDIUM : RoulettePlate.DARKER);

			if(index < segmentsComplete) {
				gfx.beginFill(index % 2 != 0 ? RoulettePlate.BLUE_DARK : RoulettePlate.BLUE_LIGHT);
			}

			gfx.drawPolygon(points)

			gfx.beginFill(RoulettePlate.DARK, 0.25);
			gfx.drawCircle(0, 0, smallSize);

			gfx.beginFill(RoulettePlate.MEDIUM);
			gfx.drawCircle(0, 0, 120);
		}

		this.addChild(gfx);

		const wheel = PIXI.Sprite.fromImage(Assets.WHEEL);
		wheel.anchor.set(0.5, 0.5);
		wheel.scale.set(0.5);
		wheel.alpha = 0.7;
		// this.addChild(wheel);
	}

	public toDegrees(radians: number): number {
        return radians * 180 / Math.PI;
    }

    public toRadians(angle: number): number {
        return angle * Math.PI / 180;
    }
}